// tabBar 

import React, { Component } from "react";

import { Route } from "react-router-dom";

// tabBar
import { TabBar } from "antd-mobile";

// tabBar
import TabBarConfig from "../../utils/tabBarConfig";

// 
import "./index.css";

import Index from "../Index";
import House from "../House";
import Profile from "../Profile";

class Home extends Component {
  state = {
    // 
    selectedTab: this.props.location.pathname,
  };

  componentDidMount() {
    this.listenRouter()
  }


  // 
  listenRouter = () => {
    // console.log(this.props)
    //  => PureComponent
    // PureComponent
    // 
    // this

    this.unlisten = this.props.history.listen((location) => {
      // console.log(location)
      if (location.pathname !== this.state.selectedTab) {
        // console.log(111)
        this.setState({
          selectedTab: location.pathname,
        });
      }
    });
    // console.log(this.unlisten)  //  () { checkDOMListeners(-1);unlisten(); }
  };

  
  // 
  componentWillUnmount() {
    // 
    this.unlisten();
  }

  // TabBar
  renderTabBar = () => {
    return (
      <TabBar
        unselectedTintColor="#949494"
        tintColor="#33A3F4"
        barTintColor="white"
      >
        {TabBarConfig.map((item) => (
          <TabBar.Item
            title={item.title}
            key={item.path}
            icon={<i className={`iconfont ${item.icon}`}></i>}
            selectedIcon={<i className={`iconfont ${item.icon}`}></i>}
            selected={this.state.selectedTab === item.path}
            // =>
            onPress={() => {
              // this.setState({
              //   selectedTab: item.path,
              // });
              this.props.history.push(item.path);
            }}
          ></TabBar.Item>
        ))}
      </TabBar>
    );
  };

  render() {
    // console.log(this.props)
    // console.log(this.props.location.pathname);
    return (
      <div className="home">
        {/*  */}
        <Route exact path="/home" component={Index}></Route>
        <Route path="/home/house" component={House}></Route>
        <Route path="/home/profile" component={Profile}></Route>

        {/* tabBar */}
        <div className="tabBar">{this.renderTabBar()}</div>
      </div>
    );
  }
}

export default Home;
