// 

import { getCityInfo } from "./api/City";


// API------------------------------
const getCityName = async () => {
  return new Promise((resolve, reject) => {
    let myCity = new window.BMap.LocalCity();
    myCity.get((res) => {
      // console.log(res) // {center: J, level: 12, name: "", code: 355}
      resolve(res.name);
    });
  });
};

//   -------------------------------------
// 1.Promise=>asyncawaitresolve
//       2.=>localStorage

// key
const CURR_CITY = "curr_city";

// 
// 1.
export function setLocal(key,val) {
    localStorage.setItem(key,val)
}

// 2.
export function getLocal (key) {
    return localStorage.getItem(key)
}

// 3.
export function delLocal (key) {
    localStorage.removeItem(key)
}

export async function getCurrCity() {
  // 
  let currCity = JSON.parse(getLocal(CURR_CITY));

  // 
  let res = await getCityName();
  // console.log(res)
  //   
  let realName = res.substr(0, 2);
  // console.log(realName)

  // 
  if (!currCity) {
    // 
    //  API Promise=>resolve
    return new Promise(async (resolve, reject) => {
      // IPLocalCity =>
      // 
        // 
        const { status, data } = await getCityInfo(realName);
        // console.log(data) // {label: "", value: "AREA|dbf46d32-7e76-1196"}
        if (status === 200) {
          // 
         setLocal(CURR_CITY, JSON.stringify(data));
          // 
          resolve(data);
        } else {
          reject("error");
        }     
    });
  } else {
    // 
    return Promise.resolve(currCity);
  }
}

export { CURR_CITY }
