import React from 'react'

import { Flex, Toast } from 'antd-mobile'

import Filter from './components/Filter'
// 
// styles:
import styles from './index.module.css'
import { getHouseInfoList } from '../../utils/api/House'
import { getCurrCity } from '../../utils'

import {List, AutoSizer, InfiniteLoader} from 'react-virtualized';
import HouseItem from '../../components/HouseItem'
import { BASE_URL } from '../../utils/axios'
import NoHouse from '../../components/NoHouse'


export default class HouseList extends React.Component {

  state = {
    // 
    list:[],
    // 
    count:0
  } 

// 
  renderHouseItem = ({
    key, // Unique key within array of rows
    index, // Index of row within collection
    isScrolling, // The List is currently being scrolled
    isVisible, // This row is visible within the List (eg it is not an overscanned row)
    style, // Style object to be applied to row (to position it)
  }) => {
    //  => 
    const {list} = this.state
    // 
    const item = list[index]
    // console.log(item)  // 

    // item
    if (!item) {
      return (
        <div style={style} key={key}>
          <p className={styles.loading}></p>
        </div>
      )
    }


    // houseImg   src 
    // item.houseImg = `${BASE_URL}${item.houseImg}`
    item.src = `${BASE_URL}${item.houseImg}`
    return (
      <HouseItem {...item} key={key} style={style} onClick={()=>{
        // this.props.history.push('/detail/'+item.houseCode)

        //  (hash)  (history) push
        // 1. history => H5
        // this.props.history.push('/detail/'+item.houseCode, { id: item.houseCode, a: '' })

        // 2. hash
        this.props.history.push({ pathname: '/detail/'+ item.houseCode, state: { id: item.houseCode, a: 'hash' }})

      }}></HouseItem>     
    );
  }


  async componentDidMount() {
    // id
    let {value} = await getCurrCity()
    // console.log(value)
    // idthis
    this.cityId = value
    // ,,
    this.getHouseList()
  }


  //  
  // 
  // filters:
  onFilter = (filters) => {
    // console.log('', filters)
    // this
    this.filters = filters
    // ,
    // 
    this.getHouseList()
  }


  // 
  getHouseList = async () => {
    // 
    let { status, data:{ list, count } } = await getHouseInfoList(this.cityId, this.filters, 1, 20)
    // console.log(res)
    if (status === 200) {
      // 
      if (count !== 0) {
        Toast.success(`${count}!`)
      }

      this.setState({
        list,
        count
      })
    }
  }

  // 
  // 
  isRowLoaded = ({ index }) => {
    return !!this.state.list[index];  // !!: nullundefined
  }
  
  // =>=>
  loadMoreRows = ({ startIndex, stopIndex }) => {
    return getHouseInfoList(this.cityId, this.filters, startIndex, stopIndex)
      .then(res => {
        // Store response data in list...
        // console.log('',startIndex, stopIndex, res) //  20 29 {status: 200, data: {}, description: ""}
        if (res.status === 200) {
          this.setState({
            list:[...this.state.list, ...res.data.list]
          })
        }
      })
  }

  renderList = () => {
    const {count} = this.state
    // NoHouse
    return count>0?<InfiniteLoader
      isRowLoaded={this.isRowLoaded}
      loadMoreRows={this.loadMoreRows}
      rowCount={this.state.count}
      minimumBatchSize={10}  // 
    >
      {({ onRowsRendered, registerChild }) => (
        <AutoSizer>
          {({ height, width }) => (
            <List
              height={height}
              onRowsRendered={onRowsRendered}
              ref={registerChild}
              className={styles.houseList}
              rowCount={this.state.count}
              rowHeight={130}
              rowRenderer={this.renderHouseItem}
              width={width}
            />
          )}
        </AutoSizer>        
      )}
    </InfiniteLoader>:<NoHouse></NoHouse>
  }

  render() {
    return (
      <div className={styles.root}>
        {/*  */}
        <Filter onFilter={this.onFilter} />
        {/*  */}
        {this.renderList()}
      </div>
    )
  }
}
