export default class FilterPicker extends Component {

  // constructor:(new)1
  // constructor(props){
  //   super(props)
  //   console.log('picker=>constructor',this.props.value)
  // }

  // state1
  // value
  state = {
    // picker
    // value:[]
    value:this.props.value  // 
  }

  render() {
    const {data,cols} = this.props
    // console.log(data)
    return (
      <>
        {/*  */}
        <PickerView data={data} 
          value={this.state.value} 
          cols={cols} 
          onChange={(val)=>{
          // console.log(val)
          this.setState({
            value:val
          })
        }} />

        {/*  */}
        {/*   picker =>  */}
        <FilterFooter onOk={()=>{
          this.props.onOk(this.state.value)
        }} onCancle={this.props.onCancle} />
      </>
    )
  }
}
